<template>
  <div>
    <tab-page ref="tabPage">
      <template slot="h5StatisticsDetail">
        <data-table :data-table="dataTable" ref="dataTable">
          <div slot="conditions" style="margin-bottom:15px">
            <el-form :inline="true" :model="queryParams">
              <!--<el-form-item>-->
                <!--<el-date-picker-->
                  <!--v-model="queryParams.beginTime"-->
                  <!--type="date"-->
                  <!--placeholder="选择日期时间" size="small" style="width:170px">-->
                <!--</el-date-picker>-->
              <!--</el-form-item>-->
              <!--<span class="span-style">至</span>-->
              <!--<el-form-item>-->
                <!--<el-date-picker-->
                  <!--v-model="queryParams.endTime"-->
                  <!--type="date"-->
                  <!--placeholder="选择日期时间" size="small" style="width:170px">-->
                <!--</el-date-picker>-->
              <!--</el-form-item>-->
              <el-form-item>
                <!--<el-date-picker-->
                  <!--v-model="queryParams.dateRange"-->
                  <!--type="daterange"-->
                  <!--size="small"-->
                  <!--placeholder="选择日期范围">-->
                <!--</el-date-picker>-->

                <el-date-picker
                  v-model="queryParams.dateRange"
                  type="datetimerange"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期">
                </el-date-picker>
              </el-form-item>
              <el-form-item>
                <el-select v-model="queryParams.device"
                           clearable
                           filterable
                           placeholder="设备"
                           size="small">
                  <el-option
                    v-for="item in deviceOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-select v-model="queryParams.source"
                           ref="sourceSelect"
                           clearable
                           filterable
                           remote
                           :remote-method="remoteSourceMethod"
                           :loading="loading"
                           placeholder="source"
                           size="small">
                  <el-option
                    v-for="item in sourceOptions"
                    :key="item.value"
                    :label="item.name"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-select v-model="queryParams.medium"
                           ref="mediumSelect"
                           clearable
                           filterable
                           remote
                           :remote-method="remoteMediumMethod"
                           :loading="loading"
                           placeholder="medium"
                           size="small">
                  <el-option
                    v-for="item in mediumOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-select v-model="queryParams.campaign"
                           ref="campaignSelect"
                           clearable
                           filterable
                           remote
                           :remote-method="remoteCampaignMethod"
                           :loading="loading"
                           placeholder="campaign"
                           size="small">
                  <el-option
                    v-for="item in campaignOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-select v-model="queryParams.isksjk" clearable filterable placeholder="快速借款"
                           size="small">
                  <el-option
                    v-for="item in ksjkOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="onSubmit" size="small" style="margin-top:0">查询
                </el-button>
              </el-form-item>
            </el-form>
            <el-button class="right mb-10" type="primary" size="small" icon="el-icon-document"
                       @click="exportExcel">导出excel
            </el-button>
          </div>
          <template slot="columns">
            <el-table-column type="index" label="序号" width="60"></el-table-column>
            <el-table-column sortable prop="date_str" label="日期" width="120"></el-table-column>
            <el-table-column sortable prop="hour" label="小时"></el-table-column>
            <el-table-column sortable prop="plat" label="设备" width="100"></el-table-column>
            <el-table-column sortable prop="ksjk_existing" label="快速借款用户" width="120"></el-table-column>
            <el-table-column sortable prop="source" label="source"></el-table-column>
            <el-table-column sortable prop="medium" label="medium"></el-table-column>
            <el-table-column sortable prop="campaign" label="campaign"></el-table-column>

            <el-table-column sortable prop="phones_count" label="手机号"></el-table-column>
            <el-table-column sortable prop="take_phone_new" label="留手机号新用户" ></el-table-column>
            <el-table-column sortable prop="register_count" label="注册"></el-table-column>
            <el-table-column sortable prop="activation_count" label="激活"></el-table-column>

            <el-table-column sortable prop="jinjian" label="当日进件"></el-table-column>
            <el-table-column sortable prop="approval" label="当日审核"></el-table-column>
            <el-table-column sortable prop="emitCn" label="当日放款"></el-table-column>
            <el-table-column sortable prop="emitMoney" label="放款金额"></el-table-column>jj_reg_rate
            <el-table-column sortable prop="act_reg_rate" label="激活/注册" width="90"></el-table-column>
            <el-table-column sortable prop="jj_jh_rate" label="进件/激活" width="90"></el-table-column>
            <el-table-column sortable prop="jj_reg_rate" label="进件/注册" width="90"></el-table-column>
            <el-table-column sortable prop="review_jinj_rate" label="审核/进件" width="90"></el-table-column>


          </template>
        </data-table>
      </template>
    </tab-page>
  </div>
</template>
<style scoped>
  .el-form-item__content .el-button {
    margin-top: 20px;
  }

  .el-table--border th {
    text-align: center;
  }

  .el-table th .cell {
    text-align: center;
  }

  .el-form--inline .el-form-item {
    margin-right: 2px;
  }

  .el-form-item {
    margin-bottom: 2px;
  }

  .el-dialog__footer {
    padding: 10px 0px;
    text-align: center;
  }

  .check-all-select {
    padding: 10px;
  }

  .check-all-select span.text-all {
    font-size: 14px;
    font-weight: bold;
  }

  .check-all-select label {
    float: right;
  }

  .mb-10 {
    margin-bottom: 10px;
  }
</style>
<script>
  import common from '../../../utils/common'
  import formatDate from '../../../utils/formatDate'
  import DataTable from "../../../components/DataTable"
  import TabPage from "../../../components/TabPage"
  import {Notification} from 'element-ui';
  export default{
    components: {
      DataTable,
      TabPage,
    },
    data(){
      let today = formatDate(new Date(), 'yyyy-MM-dd') + ' 00:00:00';
      let now = new Date();
      return {
        sourceCheckAll: false,
        mediumCheckAll: false,
        campaignCheckAll: false,
        deviceCheckAll: false,

        sourceOptions: [],
        mediumOptions: [],
        campaignOptions: [],
        sourceOptionsList: [],
        mediumOptionsList: [],
        campaignOptionsList: [],
        deviceOptions: [
          {
            value: 'iOS',
            label: 'iOS'
          },
          {
            value: 'Android',
            label: 'Android'
          }
        ],
        ksjkOptions: [
          {
            value: '是',
            label: '是'
          },
          {
            value: '否',
            label: '否'
          }
        ],
        initBeginTime: '',
        initEndTime: '',

        queryParams: {
          source: '',
          medium: '',
          campaign: '',
          device: '',
          isksjk: '',
          beginTime: '',//开始时间
          endTime: '',//结束时间
          dateRange: [today, now],
        },
        dataTable: {
          pageSize: 50,
          pageSizes: [10, 20, 50, 100, 1000],
          url: "/web/v1/market/queryH5DetailByHour",    //代付管理
          emptyText: '没有记录',
          showSummary: true,
          summaryMethod: this.getSummaries,
        },
        loading: false,
      }
    },
    mounted(){
      this.dataTable.queryParams = this.queryParams;
      this.initBeginTime = this.dealDate(this.queryParams.dateRange[0], 'yyyy-MM-dd');
      this.initEndTime = this.dealDate(this.queryParams.dateRange[1], 'yyyy-MM-dd');
      this.dataTable.queryParams.beginTime = this.initBeginTime + ' 00:00:00';
      this.dataTable.queryParams.endTime = this.initEndTime + ' 23:59:59';

      var defaultPage = {
        label: "H5渠道",
        name: "h5StatisticsDetail",
        slotName: 'h5StatisticsDetail',
        closable: false
      };
      this.$refs.tabPage.$emit("open-tab", defaultPage);

      this.getCampaigns();
      this.getMediums();
      this.getSources();
    },
    beforeDestroy(){
      // this.campaignOptions = [];
      // this.mediumOptions = [];
      // this.sourceOptions = [];
      // this.$refs.campaignSelect.doDestroy();
      // this.$refs.mediumSelect.doDestroy();
      // this.$refs.sourceSelect.doDestroy();

    },
    methods: {
      remoteSourceMethod(query){
        if (query !== '') {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;

            this.sourceOptions = this.sourceOptionsList.filter(item => {
              return item.name.indexOf(query) > -1;
            });
          }, 200);
        } else {
          this.sourceOptions = [];
        }
      },
      remoteMediumMethod(query){
        if (query !== '') {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;

            this.mediumOptions = this.mediumOptionsList.filter(item => {
              return item.name.indexOf(query) > -1;
            });
          }, 200);
        } else {
          this.mediumOptions = [];
        }
      },
      remoteCampaignMethod(query){
        if (query !== '') {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;

            this.campaignOptions = this.campaignOptionsList.filter(item => {
              return item.name.indexOf(query) > -1;
            });
          }, 200);
        } else {
          this.campaignOptions = [];
        }
      },
      loadLists(){
        this.$refs.dataTable.$emit('load-data');
      },
      onSubmit: function () {
        this.dataTable.queryParams = this.queryParams;
        this.initBeginTime = this.dealTime(this.queryParams.dateRange[0]);
        this.initEndTime = this.dealTime(this.queryParams.dateRange[1]);
        this.dataTable.queryParams.beginTime = this.initBeginTime ;
        this.dataTable.queryParams.endTime = this.initEndTime;

        this.dataTable.url = '/web/v1/market/queryH5DetailByHour';
        this.$refs.dataTable.$emit('load-data');
      },
      warnTime(startTime, endTime){
        if (!startTime) {
          this.$notify({
            message: '查询开始时间不能为空！',
            type: 'warning'
          });
          return true;
        }
        if (!endTime) {
          this.$notify({
            message: '查询结束时间不能为空！',
            type: 'warning'
          });
          return true;
        }
        let timeStart = new Date(startTime).getTime();
        let timeEnd = new Date(endTime).getTime();
        if (timeStart > timeEnd) {
          this.$notify({
            message: '查询开始时间不能大于结束时间',
            type: 'warning'
          });
          return true;
        }
        return false;
      },
      dealTime (time) {
        return formatDate(new Date(time), 'yyyy-MM-dd hh:mm:ss');
      },
      formatterDate (row, column) {
        return formatDate(row.date, 'yyyy-MM-dd');
      },
      dealDate (date) {
        return formatDate(new Date(date), 'yyyy-MM-dd');
      },
      getSummaries (param) {
        const {columns, data} = param;
        const sums = [];
        columns.forEach((column, index) => {
          if (index === 0) {
            sums[index] = '总计';
            return;
          }
          var values;
          if(index === 19||index === 18||index === 17||index === 16){
             values = data.map(item =>{
               if(item[column.property]!==null){
                 return Number(item[column.property].replace('%',''));
               }
             });
          }else{
            values = data.map(item => Number(item[column.property]));
          }

          if (!values.every(value => isNaN(value))) {
            sums[index] = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
            if(index==18||index==17||index==16||index==19){
              sums[index]=(sums[index]/data.length).toFixed(2)+ ' %';
            }
            //sums[index] += ' 元';
          } else {
            sums[index] = 'N/A';
          }
        });

        return sums;
      },

      exportExcel() {
        let self=this;
        var exportParams = {};
        Object.assign(exportParams, this.dataTable.queryParams);
        exportParams.pageSize = this.$refs.dataTable.total;
        exportParams.page = 1;
        this.$refs.dataTable.$emit('export-data',exportParams);
        require.ensure([], () => {
          const {export_json_to_excel} = require('vendor/Export2Excel');
          const tHeader = ['日期', '小时', '设备', '快速借款用户', 'source', 'medium', 'campaign', '手机号', '注册', '激活', '进件', '审核', '放款', '放款金额', '激活/注册', '进件/激活', '审核/进件', '留手机号新用户'];
          const filterVal = ['date', 'hour', 'plat', 'ksjk_existing', 'source', 'medium', 'campaign', 'phones_count', 'register_count', 'activation_count', 'jinjian', 'approval', 'emitCn', 'emitMoney', 'act_reg_rate', 'jj_jh_rate', 'review_jinj_rate', 'take_phone_new'];
          setTimeout(() => {
            const list = this.$refs.dataTable.dataListA;
            const data = this.formatJson(filterVal, list);
            export_json_to_excel(tHeader, data, 'H5渠道(小时)');
          }, 300);

        })
      },
      formatJson(filterVal, jsonData) {
        return jsonData.map(v => filterVal.map(j => {
          let returnValue;
          if (j == 'operator') {
            returnValue = v[j].name;
          } else {
            returnValue = v[j];
          }
          return returnValue;
        }))
      },

      getSources(){
        common.ajax({
          url: '/web/v1/options/h5/sources',
          type: 'POST',
          contentType: 'application/json',
          success: (response) => {
            if (response.data && response.data.list) {
              // this.sourceOptions = response.data.list;
              this.sourceOptionsList = response.data.list;
            }
          },
          error: (err) => {
            let error = err.message || err;
            Notification.error({
              title: '错误',
              message: error
            });
          }
        });
      },

      getMediums(){
        common.ajax({
          url: '/web/v1/options/h5/mediums',
          type: 'POST',
          contentType: 'application/json',
          success: (response) => {
            if (response.data && response.data.list) {
              // this.mediumOptions = response.data.list;
              this.mediumOptionsList = response.data.list;
            }
          },
          error: (err) => {
            let error = err.message || err;
            Notification.error({
              title: '错误',
              message: error
            });
          }
        });
      },

      getCampaigns(){
        common.ajax({
          url: '/web/v1/options/h5/campaigns',
          type: 'POST',
          contentType: 'application/json',
          success: (response) => {
            if (response.data && response.data.list) {
              // this.campaignOptions = response.data.list;
              this.campaignOptionsList = response.data.list;
            }
          },
          error: (err) => {
            let error = err.message || err;
            Notification.error({
              title: '错误',
              message: error
            });
          }
        });
      },

      checkAllSelection(selectType){
        let len = this.$refs[selectType].optionsCount;
        for (var i = 0; i < len; i++) {
          (function (index, _this) {
            setTimeout(() => {
              _this.$refs[selectType].handleOptionSelect(_this.$refs[selectType].options[index]);
            }, 0);

          })(i, this);
        }
        ;
      },
      handleCheckAllSourceChange(event) {
        this.checkAllSelection("sourceSelect");
        this.sourceCheckAll = !this.sourceCheckAll;
      },
      handleCheckAllMediumChange(event) {
        this.checkAllSelection("mediumSelect");
        this.mediumCheckAll = !this.mediumCheckAll;
      },
      handleCheckAllCampaignChange(event) {
        this.checkAllSelection("campaignSelect");
        this.campaignCheckAll = !this.campaignCheckAll;
      },
      handleCheckAllDeviceChange(event) {
        this.checkAllSelection("deviceSelect");
        this.deviceCheckAll = !this.deviceCheckAll;
      },
      setCheckAllStyle(selectedCount, selectType){
        let type = selectType + 'CheckAll';
        let type_1 = selectType + 'Select';
        if (this[type]) {
          if (selectedCount.length < this.$refs[type_1].optionsCount) {
            this[type] = !this[type];
          }
        } else {
          if (selectedCount.length === this.$refs[type_1].optionsCount) {
            this[type] = !this[type];
          }
        }
      },
      sourceChange(values){
        this.setCheckAllStyle(values, "source");
      },
      mediumChange(values){
        this.setCheckAllStyle(values, "medium");
      },
      campaignChange(values){
        this.setCheckAllStyle(values, "campaign");
      },
      deviceChange(values){
        this.setCheckAllStyle(values, "device");
      },

    },
  }
</script>
<style>
  .span-style {
    display: inline-block;
    margin-top: 8px;
    font-size: 14px;
  }
</style>
